import * as React from 'react';
import {Box, Button, Container} from "@mui/material";
import Paper from '@mui/material/Paper';
import Typography from "@mui/material/Typography";
import GroupIcon from '@mui/icons-material/Group';
import SettingsIcon from '@mui/icons-material/Settings';
import FmdGoodIcon from '@mui/icons-material/FmdGood';

const strs = ["\n",
  "\n",
  "\n",
  "\n",
  "30\n",
  "10\n",
  "\n"];

export default class Home extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
        <div>
          <div style={{backgroundImage: `url(${"img/img.png"})`, height: '600px', overflow: 'hidden'}}>
            <Typography sx={{mt: 6, margin: '120px auto 20px', fontSize: '80px', textAlign: 'center'}} color="#8d6e63">
              360
            </Typography>
            <Typography sx={{mb: 1.5, margin: '10px 10px', fontSize: '30px', textAlign: 'center'}} color="#8d6e63">
              
            </Typography>
            <Typography sx={{mb: 1.5, margin: '30px 10px', fontSize: '30px', textAlign: 'center'}} color="#8d6e63">
              <Button variant={"contained"} size={"large"} style={{margin:'0 20px'}}></Button>
              <Button variant={"outlined"} size={"large"} style={{color:'#8d6e63', border:'1px solid #8d6e63'}}></Button>
            </Typography>

          </div>
          <Container>
            <Box
                sx={{
                  display: 'flex',
                  flexWrap: 'wrap',
                  '& > :not(style)': {
                    m: 5,
                    width: 300,
                    height: 260,
                  },
                  marginTop:'40px'
                }}
            >
              {/**/}
              <Paper elevation={0} style={{margin:'0 auto'}}>
                <GroupIcon
                    sx={{
                      textAlign: 'center',
                      display: 'block',
                      margin: '10px auto',
                      fontSize: '45px',
                      color: '#8d6e63'
                    }}/>
                <Typography variant="h5" component="div" sx={{textAlign: 'center'}}>
                  360
                </Typography>
                <Typography sx={{mb: 1.5, margin: '10px 10px', lineHeight: '30px'}} color="text.secondary">
                  
                </Typography>
              </Paper>

              {/**/}
              <Paper elevation={0} style={{margin:'0 auto'}}>
                <SettingsIcon
                    sx={{
                      textAlign: 'center',
                      display: 'block',
                      margin: '10px auto',
                      fontSize: '45px',
                      color: '#8d6e63'
                    }}/>
                <Typography variant="h5" component="div" sx={{textAlign: 'center'}}>
                  360
                </Typography>
                <Typography sx={{mb: 1.5, margin: '10px 10px'}} color="text.secondary">
                  
                </Typography>
                <Typography sx={{mb: 1.5, margin: '10px 10px'}} color="text.secondary">
                  +
                </Typography>
                <Typography sx={{mb: 1.5, margin: '10px 10px'}} color="text.secondary">
                  +
                </Typography>
              </Paper>

              {/**/}
              <Paper elevation={0} style={{margin:'0 auto'}}>
                <FmdGoodIcon
                    sx={{
                      textAlign: 'center',
                      display: 'block',
                      margin: '10px auto',
                      fontSize: '45px',
                      color: '#8d6e63'
                    }}/>
                <Typography variant="h5" component="div" sx={{textAlign: 'center', margin: '5px'}}>
                  360
                </Typography>
                {strs.map(str => (<Typography key={str} sx={{lineHeight: '30px'}} color="text.secondary">
                  {str}
                </Typography>))}
              </Paper>
            </Box>
          </Container>
        </div>

    )
  }

  componentDidMount() {
  }
}
